import axios from "axios";
import {
    ADD_DESCRIPTION,
    ADD_FEED,
    ADD_POST,
    CLEAR_POST, LIKE_POST, UNLIKE_POST,
    SET_CURRENT_FEEDS,
    SHOW_MEDIA_LOADING,
    SHOW_PREVIEW
} from "./types";

const endpoint = `http://localhost:8080`;
// const endpoint = ``;

export const createPost = (data)=>dispatch=>{
    axios.post(`${endpoint}/api/post`, data).then(res=>{
        dispatch({
            type:ADD_FEED,
            payload:res.data.data
        });
        dispatch({
            type:SHOW_MEDIA_LOADING,
            payload:false
        });
        dispatch({
            type:CLEAR_POST,
            payload:{description:'', attachments:[], preview:''}
        })
    }).catch(err=>{
        dispatch({
            type:CLEAR_POST,
            payload:{description:'', attachments:[], preview:''}
        })
    })
};

export const updateDescription = (data)=>dispatch=>{
    dispatch({
        type:ADD_DESCRIPTION,
        payload:data
    });
};
export const showPreview = (data)=>dispatch=>{
    dispatch({
        type:SHOW_PREVIEW,
        payload:data
    })
};
export const createMedia = (mediaData)=>dispatch=>{

    const formData = new FormData();
    formData.append('postImage',mediaData);
    const config = {
        headers:{'content-type':'multipart/form-data'}
    };
    axios.post(`${endpoint}/api/post/media`, formData, config)
        .then(res=>{
            if(res.err){
                dispatch({
                    type:CLEAR_POST,
                    payload:{description:'', attachments:[], preview:''}
                });
                dispatch({
                    type:SHOW_MEDIA_LOADING,
                    payload:false
                })
            }
            if(res.data.success){
                let media = {
                    id:res.data.data._id,
                    url:res.data.data.url
                };
                dispatch({
                    type:ADD_POST,
                    payload:media
                })
                dispatch({
                    type:SHOW_MEDIA_LOADING,
                    payload:false
                })
            }
        })
        .catch(err=>{
            dispatch({
                type:CLEAR_POST,
                payload:{description:'', attachments:[], preview:''}
            })
        })
};

export const showLoading = (data)=>dispatch=>{
    dispatch({
        type:SHOW_MEDIA_LOADING,
        payload:data
    })
};

export const likePost = (data)=>dispatch=>{
    axios.post(`${endpoint}/api/post/${data}/like`)
    .then(res=>{
        console.log(res)
        if(res.data.success){
            dispatch({
                type:LIKE_POST,
                payload:data
            })
        }
    })
    .catch(err=>{
        console.log(err);
    });
};

export const getWhoLikes = (data)=>dispatch=>{
    axios.get(`${endpoint}/api/post/${data}/likes`)
        .then(res=>{
            console.log(res)
        })
        .catch(err=>{
            console.log(err);
        })
}

export const unlikePost = (data)=>dispatch=>{
    axios.post(`${endpoint}/api/post/${data}/unlike`)
        .then(res=>{
            if(res.data.success){
                dispatch({
                    type:UNLIKE_POST,
                    payload:data
                })
            }
        })
        .catch(err=>{
            console.log(err);
        });
};

export const getPosts = ()=>dispatch=>{
    axios.get(`${endpoint}/api/post`).then((res)=>{
        dispatch({
            type: SET_CURRENT_FEEDS,
            payload:res.data.data
        });
    })
};