import axios from "axios";
import { SET_PROFILE_PAGE, SET_FOLLOW_UNFOLLOW } from "./types";

const endpoint = `http://localhost:8080`;
// const endpoint = ``;

export const getUser = (user)=>dispatch=>{
    axios.get(`${endpoint}/api/user/${user}`).then((res)=>{
        dispatch({
            type: SET_PROFILE_PAGE,
            payload:res.data
        })
    })
};

export const createAvatar = (user, avatar)=>dispatch=>{
    const formData = new FormData();
    formData.append('avatar',avatar);
    const config = {
        headers:{'content-type':'multipart/form-data'}
    };
    axios.post(`${endpoint}/api/user/${user}/avatar`, formData, config)
        .then(res=>{
            console.log(res)
        })
        .catch(err=>{console.log(err)})
};

export const followUser = (user)=>dispatch=>{

  axios.post(`${endpoint}/api/user/${user}/follow`)
      .then(res=>{
          dispatch({
              type:SET_FOLLOW_UNFOLLOW,
              payload:true
          });

      })
      .catch(err=>{
          console.log(err);
      })
};

export const unfollowUser = (user)=>dispatch=>{
    axios.post(`${endpoint}/api/user/${user}/unfollow`)
        .then(res=>{
            dispatch({
                type:SET_FOLLOW_UNFOLLOW,
                payload:false
            })
        })
        .catch(err=>{
            console.log(err);
        })
};