import {ADD_DESCRIPTION, ADD_FEED, ADD_POST,LIKE_POST,UNLIKE_POST, CLEAR_POST, SET_CURRENT_FEEDS, SHOW_PREVIEW,SHOW_MEDIA_LOADING} from '../actions/types';
import _ from 'lodash';


const initialState = {
    post:{
        showLoading:false,
        description:'',
        attachments:[],
        preview:null
    },
    feeds:[]
};


export default function(state = initialState, action){

    switch (action.type) {
        case ADD_DESCRIPTION:
            let dp = state.post;
            dp.description = action.payload;
            return {
                ...state,
                post:dp
            };
        case SHOW_PREVIEW:
            let  previewField = state.post;
            previewField.preview = action.payload;
            return {
                ...state,
                post:previewField
            };
        case SHOW_MEDIA_LOADING:
            let loadingField = state.post;
            loadingField.showLoading = action.payload;
            return {
                ...state,
                post:loadingField
            };
        case ADD_POST:
            let {post} = state;

            post.attachments.unshift(action.payload.id);
            return {
                ...state,
                post
            };
        case LIKE_POST:
            let feeds_likes = state.feeds;
            let modifiedLikesFeeds = _.map(feeds_likes,(feed)=>{
                    if(feed._id === action.payload){
                        feed.likedByMe = true
                        feed.likeCount = feed.likeCount +1;
                    }
                    return feed
            });
            return {
                ...state,
                feeds :modifiedLikesFeeds
            };
        case UNLIKE_POST:
            let feeds_unlikes = state.feeds;
            let modifiedUnlikesFeeds = _.map(feeds_unlikes,(feed)=>{
                if(feed._id === action.payload){
                    feed.likedByMe = false;
                    feed.likeCount = feed.likeCount -1;
                }
                return feed
            });
            return {
                ...state,
                feeds :modifiedUnlikesFeeds
            };
        case CLEAR_POST:
            let clearPost = state.post;
            clearPost=action.payload;
            return {
                ...state,
                post:clearPost
            }
        case ADD_FEED:
            let feeds = state.feeds;
            feeds.unshift(action.payload);
            return {
                ...state,
                feeds:feeds
            };
        case SET_CURRENT_FEEDS:
            return {
                ...state,
                feeds:action.payload
            };
        default:
            return state;
    }
}